*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body{
    width: 100vw;
    height: 100vh;
    /* display: grid;
    grid-template-columns: .30fr 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas: 
    "header header"
    "aside container"
    "footer footer"; */
  }


  header {
    background-color: #aaaaaa;
  }
  
  #header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17.5px;
    width: 100%;
    background: #b9b9b9;
  }
  
  .logo{
    width: 40px;
    height: 40px;
  }
  
  .links-container{
    display: flex;
  }
  
  .links, .logout{
    list-style: none;
    background: #bb4e0f;
    padding: 10px 20px;
    margin-right: 10px;
    border-radius: 5px;
  }
  
  .links a{
    text-decoration: none;
    color: #f8f8f8;
  }
  
  .aside{
    grid-area: aside;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 80vh;
    background: #c2c1c1;
    padding: 20px;
  }
  
  .quick-links{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .quick-links a{
    text-decoration: none;
    color: #383838;
    padding: 10px;
    font-size: 18px;
  }
  
  .active{
    color: #bb4e0f !important;
  }
  
  .logout{
    text-decoration: none;
    color: #f8f8f8;
  }

  .box{
    border: 2px solid green;
     padding: 10px;
      margin: 20px;
      
  }
  
  /* .container{
    grid-area: container;
    width: 100%;
    height: 480px;
    display: flex;
    align-items: center;
    justify-content: center;
  } */
  
  .grid{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  footer{
    grid-area: footer;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 15px;
    background: #3d3d3d;
    border: 2px solid black;
  }
  
  .media{
    width: 40px;
    height: 40px;
    margin: 0 10px;
  }
  
  .container form{
    display: flex;
    flex-direction: column;
  }
  
  .container form h1{
    font-size: 44px;
    letter-spacing: 3px;
    font-weight: 500;
    text-align: center;
  }
  
  .inputs{
    display: flex;
    padding: 10px;
    justify-content: space-between;
  }
  
  .inputs input, textarea{
    padding: 10px 20px;
    font-size: 18px;
    border: none;
    background: #eceaea;
  }
  
  .inputs label{
    padding: 10px 20px;
    font-size: 25px;
    margin-right: 10px;
  }
  
  .buttons{
    display: flex;
    padding: 10px;
    justify-content: space-between;
  }
  
  .buttons input{
    padding: 20px 25px;
    font-size: 22px;
    background: rgb(48, 80, 0);
    border: none;
    outline: none;
    color: #f8f8f8;
  }
  
  .forgot{
    font-size: 20px;
    text-align: center;
    text-decoration: none;
    margin-top: 10px;
  }
  
  .blog h1{
    font-size: 44px;
    letter-spacing: 3px;
    margin-bottom: 50px;
    text-align: center;
  }
  
  .blog p{
    width: 400px;
  }
  
  .container.home{
    flex-direction: column;
    width: 100%;
    padding: 20px;
  }
  
  .content{
    display: flex;
    justify-content: space-between;
  }
  
  .content > img{
    width: 350px;
    height: 300px;
  }
  
  .content-item{
    width: 500px;
    padding: 10px;
  }
  
  .content-item img{
    width: 100%;
  }
  
  textarea{
    resize: none;
    height: 100px;
  }
  .signup h1{
    font-size: 22px;
  }
  .signup .inputs{
    padding: 5px;
  }
  
  .container.reporting{
    height: auto !important;
  }
  
  .container .report{
    width: 70%;
  }
  
  .report table{
    margin-bottom: 50px;
    width: 100%;
    font-size: 25px;
    background: #adadad;
  }
  
  .report table th{
    border: 1px solid #383838;
    padding: 10px 0;
    font-weight: 500;
  }
  
  .report table td{
    text-align: center;
    border: 1px solid #383838;
  }
  

  .container {
    margin: 50px;
  }
  
  .card {
    border: 2px solid black;
    margin: 10px;
    padding: 10px;
  }
  
  
  .btn {
    padding: 10px;
    background-color: blue;
    color: white;
    margin: 10px;
    border-radius: 10%;
  }
  
  table, th, td{
    /* border: 2px solid black; */
    border-collapse: collapse;
    padding: 10px;
    margin: 10px;
  }

  aside {
    width: 20%;
    height: 100%;
    padding: 20px;
    margin-right: 15px;
    float: left;
    font-style: italic;
    background-color: rgb(195, 255, 195);
    border: 2px solid green;
  }

  aside > ul > li{
    list-style: none;
    font-weight: 900;
    font-size: 20px;
    margin: 20px;
  }
  aside > ul > li> a{
    text-decoration: none;
    color: black;
  }


  main{
    padding: 30px;
    margin: 30px;
  }

  @media (max-width: 500px) {

    #header {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      padding: 17.5px;
      width: 100%;
      background: #b9b9b9;
    }

    #header div{
      display: flex;
      flex-direction: column;
      align-items: center;
      /* justify-content: space-between; */
      /* padding: 17.5px; */
      width: 100%;
      background: #b9b9b9;
    }

    .grid{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

    #home{
      display: flex;
      flex-direction: column;

    }
    main{
      padding: 10px;
      margin: auto;
    }

    .inputs{
      display: flex;
      flex-direction: column;
    }
  }